import React, { useEffect, useState } from "react"
import styled from "styled-components"
import axios from "axios"
import moment from "moment"
import {
  AccessTime,
  AccountCircle,
  EventAvailable,
  KeyboardArrowLeft,
  KeyboardArrowRight,
} from "@material-ui/icons"
import bien from "../img/Bien.jpg"
import bien1 from "../img/brlogin.jpg"
import bien2 from "../img/brlogin2.jpg"
import { useDispatch, useSelector } from "react-redux"
import { setProduct } from "../redux/actions/productActions"
import { Link } from "react-router-dom"
import { moblie } from "../contants/sizeScreen"
import { urlTour } from "../contants/urlContants"

const fs16px = {
  fontSize: "16px",
}
const fs14px = {
  fontSize: "14px",
}
const Container = styled.div`
  padding-top: 60px;
  background-color: #292828;
`

const Wrapper = styled.div``
const WrapperBanner = styled.div`
  height: 70vh;
  display: flex;
  overflow: hidden;
  position: relative;

  @media screen and (max-width: ${moblie}) {
    height: 40vh;
  }
`
const ArrowLeft = styled(KeyboardArrowLeft)`
  position: absolute;
  top: 50%;
  left: 100px;
  transform: translateY(-50%);
  padding: 10px;
  background-color: #eadddd;
  border-radius: 50%;
  cursor: pointer;

  @media screen and (max-width: ${moblie}) {
    margin: 0;
    padding: 0;
    left: 10px;
  }
`
const ArrowRight = styled(KeyboardArrowRight)`
  position: absolute;
  top: 50%;
  right: 100px;
  transform: translateY(-50%);
  padding: 10px;
  background-color: #eadddd;
  border-radius: 50%;
  cursor: pointer;

  @media screen and (max-width: ${moblie}) {
    margin: 0;
    padding: 0;
    right: 10px;
  }
`

const Banner = styled.div`
  position: relative;
  transform: translate(${(prop) => prop.changeBanner * -100}vw);
  transition: all 1s;
`
const Image = styled.img`
  width: 100vw;
  height: 70vh;

  @media screen and (max-width: ${moblie}) {
    height: 40vh;
  }
`
const Title = styled.p`
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  font-size: 2.2rem;
  text-align: center;

  @media screen and (max-width: ${moblie}) {
    margin: 0;
    padding: 0;
  }
`
const WrapperTours = styled.div`
  margin: 0 150px;
  padding: 50px 0;

  @media screen and (max-width: ${moblie}) {
    margin: 0;
    padding: 0;
    padding: 20px 0;
  }
`
const Tours = styled.div`
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 20px;

  @media screen and (max-width: ${moblie}) {
    display: block;
    padding: 0 10px;
  }
`
const ItemTour = styled.div`
  width: 100%;
  background-color: #dfd9d9;
  border-radius: 8px;

  @media screen and (max-width: ${moblie}) {
    margin-top: 10px;
  }
`
const Top = styled.div`
  position: relative;
  background-color: antiquewhite;
  height: 300px;
  border-radius: 8px;
  overflow: hidden;
`
const ImageTour = styled.img`
  width: 100%;
  height: 100%;
  border-radius: 8px;
  transition: all 0.5s;
  &:hover {
    cursor: pointer;
    transform: scale(1.1);
  }

  @media screen and (max-width: ${moblie}) {
    width: 100%;
  }
`
const CountDown = styled.p`
  margin: 0;
  position: absolute;
  bottom: 0;
  left: 0;
  width: calc(50% - 20px);
  height: 24px;
  display: flex;
  align-items: center;
  padding: 6px 10px;
  background-color: rgba(255, 255, 255, 0.5);
`
const PlaceStart = styled.p`
  margin: 0;
  position: absolute;
  bottom: 0;
  right: 0;
  width: calc(50% - 20px);
  height: 24px;
  padding: 6px 10px;
  text-align: center;
  background-color: rgba(255, 255, 255, 0.5);
`
const Center = styled.div`
  padding: 10px;
  background-color: rgba(255, 255, 255, 0.5);
  text-align: center;
`
const PlaceGo = styled(Link)`
  width: 100%;
  text-decoration: none;
  font-weight: 600;
  font-size: 17px;
  &:hover {
    cursor: pointer;
    color: #c31ea7;
  }
`
const Bottom = styled.div`
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 10px;
  padding: 10px;
  font-size: 14px;
`
const Time = styled.p`
  margin: 0;
  display: flex;
  align-items: center;
`
const TimeStart = styled.p`
  margin: 0;
  display: flex;
  align-items: center;
`
const EmptySeats = styled.p`
  margin: 0;
  display: flex;
  align-items: center;
`
const Price = styled.p`
  margin: 0;
  display: flex;
  align-items: center;
  font-size: 20px;
  color: red;
  font-weight: 600;
`
const API = [
  {
    img: bien,
    title:
      "Biển Nha Trang tuyệt đẹp. nói xạo đó,Biển Nha Trang tuyệt đẹp. nói xạo đó",
  },
  {
    img: bien1,
    title:
      "các doanh nghiệp đi du lịch không có cơ hội tham quan những thắng cảnh đẹp “hiếm có khó tìm”. Là công ty lữ hành giàu kinh nghiệm",
  },
  {
    img: bien2,
    title:
      "Trò chơi tập thể” mang tính giải trí đơn thuần không thể đem lại trải nghiệm ý nghĩa cho chuyến đi.",
  },
  {
    img: bien1,
    title:
      "Đội ngũ nhân sự tận tâm, nhiệt tình, luôn sẵn sàng “hết mình” vì khách hàng trong suốt chuyến đi.",
  },
]
const Tour = () => {
  useEffect(() => {
    window.scrollTo(0, 0)
  }, [])

  const products = useSelector((state) => state.allProducts.products)
  const language = useSelector((state) => state.setLanguage.language)

  const dispatch = useDispatch()
  useEffect(() => {
    const ListTour = async () => {
      const response = await axios
        .get(urlTour)
        .catch((err) => console.log("err", err))
      dispatch(setProduct(response.data))
    }
    ListTour()
  }, [dispatch])

  const [changeBanner, setChangeBanner] = useState(0)

  useEffect(() => {
    const timerId = setInterval(() => {
      changeBanner >= 3 ? setChangeBanner(0) : setChangeBanner(changeBanner + 1)
    }, 5000)
    return () => clearTimeout(timerId)
  }, [changeBanner])

  const handleRightBanner = () => {
    changeBanner >= 3 ? setChangeBanner(0) : setChangeBanner(changeBanner + 1)
  }

  const handleLeftBanner = () => {
    changeBanner <= 0 ? setChangeBanner(3) : setChangeBanner(changeBanner - 1)
  }
  return (
    <Container>
      <Wrapper>
        <WrapperBanner>
          {products.map((item, index) => (
            <Banner key={index} changeBanner={changeBanner}>
              <Image src={item.image[0]} />
              <Title>{item.title}</Title>
            </Banner>
          ))}
          <ArrowLeft onClick={handleLeftBanner} />
          <ArrowRight onClick={handleRightBanner} />
        </WrapperBanner>
        <WrapperTours>
          <Tours>
            {products.map((data) => (
              <ItemTour key={data._id}>
                <Top>
                  <Link to={`/tour/${data._id}`}>
                    <ImageTour src={data.image[0]} />
                  </Link>
                  <CountDown>
                    <AccessTime style={fs16px} />
                    Còn 02 ngày 01:20:18
                  </CountDown>
                  <PlaceStart>
                    {language === "VN" ? "Khởi Hành Từ" : "Departure from"}{" "}
                    {data.departureFrom}
                  </PlaceStart>
                </Top>
                <Center>
                  <PlaceGo to={`/tour/${data._id}`}>{data.name}</PlaceGo>
                </Center>
                <Bottom>
                  <Time>
                    <AccessTime style={fs14px} />{" "}
                    {language === "VN" ? "Lịch trình:" : "Schedule:"}{" "}
                    {moment(data.timeEnd).date() -
                      moment(data.timeStart).date()}{" "}
                    ngày{" "}
                    {moment(data.timeEnd).date() -
                      moment(data.timeStart).date() -
                      1}{" "}
                    đêm
                  </Time>
                  <TimeStart>
                    <EventAvailable style={fs14px} />
                    {language === "VN" ? "Khởi hành:" : "Start: "}{" "}
                    {moment(data.timeStart).utc().format("DD/MM/YYYY")}
                  </TimeStart>
                  <EmptySeats>
                    <AccountCircle style={fs14px} />{" "}
                    {language === "VN"
                      ? "Số chổ còn nhận:"
                      : "Number of seats still accepted:"}{" "}
                    {data.seats}
                  </EmptySeats>
                  <Price>
                    {language === "VN"
                      ? data.price.toLocaleString("vi-VN") + "VND"
                      : (data.price / 23000)
                          .toFixed(2)
                          .toLocaleString("en-US") + "$"}
                  </Price>
                </Bottom>
              </ItemTour>
            ))}
          </Tours>
        </WrapperTours>
      </Wrapper>
    </Container>
  )
}

export default Tour
