import React, { useEffect } from "react"
import styled from "styled-components"
import axios from "axios"
import { EventAvailable, Visibility } from "@material-ui/icons"
import { useDispatch, useSelector } from "react-redux"
import { setNews } from "../redux/actions/newsActions"
import { Link } from "react-router-dom"
import { moblie } from "../contants/sizeScreen"
import { urlNews } from "../contants/urlContants"

const fs14px = {
  fontSize: "14px",
  marginRight: "5px",
}
const Container = styled.div`
  padding-top: 60px;
  background-color: #292828;
`

const Wrapper = styled.div`
  margin: 0 150px;
  padding: 50px;
  box-shadow: 0 3px 10px 0 rgb(0 0 0 / 50%);

  @media screen and (max-width: ${moblie}) {
    padding: 0;
    margin: 0;
  }
`

const WrapperBanner = styled.div``
const BannerTour = styled.div`
  @media screen and (max-width: ${moblie}) {
    padding: 0 10px;
  }
`
const MenuBanner = styled.ul`
  margin: 0;
  padding: 0;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-template-rows: 2fr 1fr 2fr;
  grid-gap: 10px;

  @media screen and (max-width: ${moblie}) {
    display: block;
    grid-gap: 0px;
  }
`
const ItemBanner = styled.li`
  list-style: none;
  border-radius: 8px;
  position: relative;
  overflow: hidden;

  &:first-child {
    grid-column: 1/3;
    grid-row: 1/3;
    font-size: 18px;
  }
  &:nth-child(2) {
    grid-column: 3/4;
    grid-row: 1/2;
    font-size: 14px;
  }
  &:nth-child(3) {
    grid-column: 4/5;
    grid-row: 1/2;
    font-size: 14px;
  }
  &:nth-child(4) {
    grid-column: 1/2;
    grid-row: 3/4;
    font-size: 14px;
  }
  &:nth-child(5) {
    grid-column: 2/3;
    grid-row: 3/4;
    font-size: 14px;
  }
  &:nth-child(6) {
    grid-column: 3/5;
    grid-row: 2/4;
    font-size: 18px;
  }

  @media screen and (max-width: ${moblie}) {
    margin-bottom: 15px;
    font-size: 18px !important;
    height: 300px;
  }
`
const LinkBanner = styled(Link)``
const ImageBanner = styled.img`
  width: 100%;
  height: 100%;
  border-radius: 8px;
  transition: all 0.5s;
  &:hover {
    transform: scale(1.1);
  }
`
const NameBanner = styled.p`
  margin: 0;
  padding: 0;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 5px 10px;
  text-align: justify;
  color: #fff;
  font-weight: 600;
  background-color: rgb(0 0 0 / 50%);
  box-shadow: 0 3px 10px 0 rgb(0 0 0 / 50%);
`
const View = styled.div`
  position: absolute;
  top: 10px;
  right: 10px;
  min-width: 60px;
  min-height: 30px;
  padding: 5px;
  border-radius: 40%;
  font-size: 14px !important;
  background-color: rgb(0 0 0 / 50%);
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
`
const WrapperTours = styled.div`
  padding: 50px 0;

  @media screen and (max-width: ${moblie}) {
    padding: 0;
  }
`
const Tours = styled.div`
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr;
  grid-gap: 20px;

  @media screen and (max-width: ${moblie}) {
    grid-gap: 0px;
  }
`
const ItemTour = styled.div`
  background-color: rgb(0, 255, 255);
  border-radius: 8px;
`
const Top = styled.div`
  position: relative;
  background-color: antiquewhite;
  height: 250px;
  border-radius: 8px;
  overflow: hidden;
`
const ImageTour = styled.img`
  width: 100%;
  height: 100%;
  border-radius: 8px;
  transition: all 0.5s;
  &:hover {
    cursor: pointer;
    transform: scale(1.1);
  }
`

const Center = styled.div`
  background-color: rgba(255, 255, 255, 0.5);
  text-align: justify;
  padding: 10px;
`
const PlaceGo = styled(Link)`
  margin: 0;
  font-weight: 600;
  font-size: 17px;
  text-decoration: none;

  &:hover {
    cursor: pointer;
    color: #c31ea7;
  }
`
const Bottom = styled.div`
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 10px;
  padding: 10px;
  font-size: 14px;
`
const Time = styled.p`
  margin: 0;
  display: flex;
  align-items: center;
`
const TimeStart = styled.p`
  margin: 0;
  display: flex;
  align-items: center;
`

const Discription = styled.p`
  grid-column-start: 1;
  grid-column-end: 3;
  margin: 0;
  text-align: justify;

  display: -webkit-box;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: normal;
  -webkit-line-clamp: 3;
  line-height: 1.6rem;
`

// let timerId

const News = () => {
  useEffect(() => {
    window.scrollTo(0, 0)
  }, [])
  const dispatch = useDispatch()

  const news = useSelector((state) => state.allNews.news)
  // console.log(news)

  useEffect(() => {
    const ListNews = async () => {
      const response = await axios
        .get(urlNews)
        .catch((err) => console.log("err", err))
      dispatch(setNews(response.data))
    }
    ListNews()
  }, [dispatch])

  const handleUpdateView = async () => {
    await axios.put().catch((err) => console.log(err))
  }

  return (
    <Container>
      <Wrapper>
        <WrapperBanner>
          <BannerTour>
            <MenuBanner>
              {news.map(
                (item, index) =>
                  index < 6 && (
                    <ItemBanner key={item._id} onClick={handleUpdateView}>
                      <LinkBanner to={`/news/${item._id}`}>
                        <ImageBanner
                          src={item.avatar !== undefined && item.avatar}
                        />
                        <NameBanner>{item.name}</NameBanner>
                        <View>
                          <Visibility
                            style={{ fontSize: "14px", marginRight: "5px" }}
                          />
                          {item.view}
                        </View>
                      </LinkBanner>
                    </ItemBanner>
                  )
              )}
            </MenuBanner>
          </BannerTour>
        </WrapperBanner>
        <WrapperTours>
          <Tours>
            {news.map(
              (item, index) =>
                index >= 6 && (
                  <ItemTour key={item._id}>
                    <Top>
                      <Link to={`/news/${item._id}`}>
                        <ImageTour
                          src={item.avatar !== undefined && item.avatar}
                        />
                      </Link>
                    </Top>
                    <Center>
                      <PlaceGo to={`/news/${item._id}`}>{item.name}</PlaceGo>
                    </Center>
                    <Bottom>
                      <Time>
                        <EventAvailable style={fs14px} /> {item.createdAt}
                      </Time>
                      <TimeStart>
                        <Visibility style={fs14px} /> {item.view}
                      </TimeStart>
                      <Discription>
                        {/* {item.avatar !== undefined && item.avatar} */}
                      </Discription>
                    </Bottom>
                  </ItemTour>
                )
            )}
          </Tours>
        </WrapperTours>
      </Wrapper>
    </Container>
  )
}

export default News
