import React from "react"
import video from "../video/video8k.mp4"
import styled from "styled-components"

const Video = styled.video`
  width: 100%;
  height: 100vh;
  object-fit: cover;
`
const SliderVideo = () => {
  return (
    <div>
      <Video autoplay autoPlay muted loop src={video}></Video>
    </div>
  )
}

export default SliderVideo
