import React, { useEffect } from "react"
import styled from "styled-components"
import { useDispatch, useSelector } from "react-redux"
import moment from "moment"
import { useParams } from "react-router-dom"
import axios from "axios"
import { selectedProduct } from "../../redux/actions/productActions"
import { moblie } from "../../contants/sizeScreen"
import { urlTour } from "../../contants/urlContants"

const Container = styled.div`
  @media screen and (max-width: ${moblie}) {
    padding: 10px;
  }
`
const Wrapper = styled.div`
  padding: 10px;
  padding-left: 0;

  @media screen and (max-width: ${moblie}) {
    padding: 0;
  }
`
const Image = styled.img`
  width: 100%;
  height: 50vh;
  object-fit: cover;

  @media screen and (max-width: ${moblie}) {
    height: 30vh;
  }
`
const Content = styled.div``
const Name = styled.p`
  margin: 0;
  padding: 20px;
  font-size: 1.5rem;
  font-weight: 600;
  border: 1px solid #ccc;
`
const ContentTour = styled.div`
  margin: 0;
`
const Menu = styled.ul`
  margin: 0;
  padding: 0;
  border: 1px solid #ccc;
`
const Item = styled.li`
  list-style: none;
  margin: 20px;
  border-bottom: 1px solid #ccc;
  display: flex;
  padding-bottom: 5px;
`
const Title1 = styled.p`
  margin: 0;
  font-weight: 600;
  margin-right: 5px;
`
const Title2 = styled.p`
  margin: 0;
`

const TitleTour = () => {
  const tour = useSelector((state) => state.product.products)

  const { id } = useParams()
  const dispatch = useDispatch()
  const url = `${urlTour}/${id}`

  useEffect(() => {
    const fetchTourDetail = async () => {
      const response = await axios
        .get(url)
        .catch((err) => console.log("err", err))
      dispatch(selectedProduct(response.data))
    }
    fetchTourDetail()
  }, [dispatch, url])
  // console.log("tour", tour)

  let img = ""
  if (tour.image) {
    img = tour.image[0]
  }
  return (
    <Container>
      <Wrapper>
        <Image src={img} />
        <Content>
          <Name>{tour.name}</Name>
          <ContentTour>
            <Menu>
              <Item>
                <Title1>Mã Tour:</Title1>
                <Title2>{tour._id}</Title2>
              </Item>
              <Item>
                <Title1>Giá:</Title1>
                <Title2>
                  {tour.price !== undefined &&
                    tour.price.toLocaleString("vi-VN")}{" "}
                  VND
                </Title2>
              </Item>
              <Item>
                <Title1>Thời gian đi: </Title1>
                <Title2>
                  {moment(tour.timeStart).utc().format("DD/MM/YYYY")}
                </Title2>
              </Item>
              <Item>
                <Title1>Thời gian về: </Title1>
                <Title2>
                  {moment(tour.timeEnd).utc().format("DD/MM/YYYY")}
                </Title2>
              </Item>
              <Item>
                <Title1>Tổng thời gian: </Title1>
                <Title2>
                  {moment(tour.timeEnd).date() - moment(tour.timeStart).date()}{" "}
                  ngày{" "}
                  {moment(tour.timeEnd).date() -
                    moment(tour.timeStart).date() -
                    1}{" "}
                  đêm
                </Title2>
              </Item>
            </Menu>
          </ContentTour>
        </Content>
      </Wrapper>
    </Container>
  )
}

export default TitleTour
