import React from "react"
import { useSelector } from "react-redux"
import { Link } from "react-router-dom"
import styled from "styled-components"
import { moblie } from "../contants/sizeScreen"

const Container = styled.div`
  margin: 40px;

  @media screen and (max-width: ${moblie}) {
    margin: 10px;
  }
`
const Title = styled.h2`
  margin-left: 40px;
  font-weight: 500;
  color: #212121;

  @media screen and (max-width: ${moblie}) {
    margin-left: 0;
  }
`
const Wrapper = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0 20px;

  @media screen and (max-width: ${moblie}) {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr 1fr 1fr 1fr;
    padding: 0;
    grid-gap: 10px;
    height: 100vh;
  }
`
const Place = styled.div`
  flex: 1;
  width: 100%;
  height: 50vh;
  margin: 0 20px;
  position: relative;
  cursor: pointer;
  border-radius: 8px;
  overflow: hidden;

  @media screen and (max-width: ${moblie}) {
    margin: 0;
    height: 100%;

    &:first-child {
      grid-column: 1/2;
      grid-row: 1/3;
    }
    &:nth-child(2) {
      grid-column: 2/3;
      grid-row: 2/4;
    }
    &:nth-child(3) {
      grid-column: 1/2;
      grid-row: 3/5;
    }
    &:nth-child(4) {
      grid-column: 2/3;
      grid-row: 4/6;
    }
    &:nth-child(5) {
      grid-column: 1/2;
      grid-row: 5/7;
    }
  }
`
const Image = styled.img`
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: all 0.5s;

  :hover {
    transform: scale(1.1);
  }
`
const Name = styled.h3`
  position: absolute;
  left: 0;
  right: 0;
  bottom: -20px;
  padding: 5px;
  color: #fff;
  text-align: center;
  background-color: #5c5a5a;
`

const FaveritePlaces = ({ products }) => {
  console.log(products)
  const language = useSelector((state) => state.setLanguage.language)

  return (
    <Container>
      <Title>
        {language === "VN" ? "Địa danh nổi tiếng" : "Famous places"}
      </Title>
      <Wrapper>
        {products.map((item, index) => (
          <Place key={item._id}>
            <Link to={`/tour/${item._id}`}>
              <Image src={item.image[0]} />
              <Name>
                {item.name} {index}
              </Name>
            </Link>
          </Place>
        ))}
      </Wrapper>
    </Container>
  )
}

export default FaveritePlaces
