import validator from "validator"


const validateAccountLogin = (userName, email, password)=>{
    const message = {}
 
    
    if(validator.isEmpty(userName)){
        message.userNameVN = "Vui lòng nhập tên tài khoản!"
        message.userNameENG = "Plese enter user name!"
    }
    if (validator.isEmpty(password)) {
        message.passwordVN = 'Vui lòng nhập mật khẩu';
        message.passwordEND = 'Please enter your password';
    } 

    if(validator.isEmpty(email)){
        message.emailVN = "Vui lòng nhập email!"
        message.emailENG = "Please enter email!"
    }

    return message
}
export default validateAccountLogin